body {
  font-family: Arial, sans-serif;
  background-color: #f2f2f2;
  color: #333;
}
.navbar, .container {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px; 
}
.navbar {
  background-color: #00000f; 
  color: #fff; 
}
.container {
  background-color: #fff;
}
h1 {
  text-align: center;
  margin-bottom: 20px;
  color: #f2f2f2;
}
h4 {
  color: #666;
  margin-top: 20px;
}
p {
  line-height: 1.6;
}